import { SpcRuleId } from "./types";

// Higher number = higher precedence (stronger rule).
// Align exactly with SQL v2.6a conflict ranking used to compute PrimeDirection:
// Trend (4) > Shift (3) > TwoSigma (2) > SinglePoint (1)
export const RULE_RANK_BY_ID: Record<SpcRuleId, number> = {
	[SpcRuleId.SinglePoint]: 1,
	[SpcRuleId.TwoSigma]: 2,
	[SpcRuleId.Shift]: 3,
	[SpcRuleId.Trend]: 4,
};

// XmR constants to mirror standard SPC formulas (SQL v2.6a parity)
// d2 for n=2
export const D2 = 1.128 as const;
// Moving range UCL factor
export const MR_UCL_FACTOR = 3.267 as const;
// 3œÉ factor for XmR limits: mean ¬± 2.66 * MRbar
export const XMR_THREE_SIGMA_FACTOR = 2.66 as const;

// T-chart transform exponent (SQL v2.6a): y = t^T_EXP, limits computed in y-space, then back-transformed
export const T_TRANSFORM_EXP = 0.2777 as const;
